
body,
div,
span,
header,
footer,
nav,
section,
aside,
article,
ul,
dl,
dt,
dd,
li,
a,
p,
h1,
h2,
h3,
h4,
h5,
h6,
i,
b,
textarea,
button,
input,
select,
figure,
figcaption {
  padding: 0;
  margin: 0;
  list-style: none;
  font-style: normal;
  text-decoration: none;
  border: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
  font-size: 100px;
  font-size: 0.24rem;
}
body:focus,
div:focus,
span:focus,
header:focus,
footer:focus,
nav:focus,
section:focus,
aside:focus,
article:focus,
ul:focus,
dl:focus,
dt:focus,
dd:focus,
li:focus,
a:focus,
p:focus,
h1:focus,
h2:focus,
h3:focus,
h4:focus,
h5:focus,
h6:focus,
i:focus,
b:focus,
textarea:focus,
button:focus,
input:focus,
select:focus,
figure:focus,
figcaption:focus {
  outline: none;
}
input[type="button"],
input[type="submit"],
input[type="search"],
input[type="reset"] {
  -webkit-appearance: none;
}
input::-webkit-input-placeholder {
  color: #A7ABAF;
}
input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #A7ABAF;
}
input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #A7ABAF;
}
input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #A7ABAF;
}
textarea {
  -webkit-appearance: none;
}
html,
body {
  height: 100%;
  width: 100%;
}
.clear:after {
  content: '';
  display: block;
  clear: both;
}
.clear {
  zoom: 1;
}
.vux-popup-dialog.vux-popup-right {
  overflow: hidden;
}
.iconfont {
  font-size: 0.24rem;
  color: #A7ABAF;
}
/*评论*/
.Detailpopup {
  /*去掉textarea边框*/
}
.Detailpopup .stitle {
  margin: 0 0.4rem;
  padding: 0.25rem 0;
  border-bottom: 1px solid #D8D8D8;
  height: 0.6rem;
  line-height: 0.6rem;
}
.Detailpopup .stitle p {
  font-size: 0.32rem;
  display: inline-block;
  color: #1C1C1C;
  float: left;
}
.Detailpopup .stitle button.weui-btn {
  float: right;
  margin: 0 0 0 0.3rem;
}
.Detailpopup .stitle .weui-btn {
  border-radius: 0.1rem;
  width: 1.1rem;
  height: 0.6rem;
  line-height: 0.6rem;
  padding: 0;
  text-align: center;
  font-size: 0.28rem;
}
.Detailpopup .stitle .weui-btn:after {
  border: none;
}
.Detailpopup .stitle .weui-btn_primary {
  background: #487D61;
}
.Detailpopup .stitle .weui-btn_default {
  background: #FFFFFF;
  border: 1px solid #487D61;
  color: #487D61;
}
.Detailpopup .weui-cells {
  position: static!important;
  margin: 0.3rem 0.4rem;
}
.Detailpopup .weui-cells .weui-cell {
  padding: 0;
}
.Detailpopup .weui-textarea {
  font-size: 0.28rem;
  line-height: 0.34rem;
}
.Detailpopup .weui-textarea-counter {
  color: #A7ABAF;
  font-size: 0.28rem;
  line-height: 0.32rem;
}
.Detailpopup .weui-textarea-counter span {
  font-size: 0.28rem;
}
/*菜单边栏弹出层*/
.asideMenu-box {
  width: 5.52rem;
  height: 100%;
  background: #fff;
  position: relative;
}
.asideMenu-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 0.98rem;
  border-top: 1px solid #DBD7D7;
}
.asideBtn {
  width: 2.76rem!important;
  height: 0.98rem!important;
  font-size: 0.3rem!important;
  line-height: 0.98rem!important;
  float: left!important;
  padding: 0!important;
  margin: 0!important;
  border: 0!important;
  border-radius: 0!important;
}
.asideBtn:after {
  border: 0!important;
  border-radius: 0!important;
}
.resetBtn {
  background: #fff!important;
  color: #757171!important;
}
.submitBtn {
  background: #487D61!important;
  color: #fff!important;
}
.asideMenu-content {
  padding: 0.3rem 0.3rem 1rem;
  overflow-y: auto;
  overflow-x: hidden;
  position: absolute;
  top: 0;
  background: #fff;
  bottom: 1rem;
}
.asideMenu-content h3 {
  font-size: 0.28rem;
  color: #757171;
  display: block;
  margin: 0.5rem 0 0.3rem;
}
.vux-checker-box {
  overflow: hidden;
  width: 5.2rem;
}
.vux-checker-item {
  float: left;
  font-size: 0.24rem;
  width: 1.12rem;
  height: 0.63rem;
  line-height: 0.63rem;
  padding: 0 0.18rem;
  border-radius: 0.1rem;
  background: #EAEDEB;
  color: #777D84;
  text-align: center;
  margin: 0 0.24rem 0.24rem 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.checker-selected {
  background: #A1C07A;
  color: #fff;
}
/*轮播*/
.banner {
  width: 100%;
  height: 4.37rem;
  background: #fff;
  position: relative;
}
.banner .vux-slider {
  width: 100%;
  height: 100%;
}
.banner .vux-swiper-item img {
  display: block;
  width: 100%;
  height: 2.8rem;
}
.banner .banner-txt {
  background: #FFF;
  position: absolute;
  bottom: 0;
  top: 2.8rem;
  padding: 0 0.4rem;
}
.banner .banner-txt h2 {
  font-size: 0.34rem;
  color: #1C1C1C;
  width: 5.68rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 1;
  margin: 0.4rem 0 0.18rem;
  font-weight: normal;
}
.banner .banner-txt h3 {
  font-size: 0.24rem;
  color: #A7ABAF;
  font-weight: normal;
  line-height: 1;
  width: 5.68rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.banner .banner-page {
  font-size: 0.24rem;
  color: #A7ABAF;
  position: absolute;
  bottom: 0.37rem;
  right: 0.4rem;
}
.banner .banner-page span {
  color: #1C1C1C;
  font-size: 0.34rem;
}
/*公共标签样式*/
.weui-cells_radio {
  max-height: 4rem;
  overflow-y: auto;
  padding: 0.1rem 0;
  background: #FFFFFF;
}
.weui-cells_radio .weui-cell {
  margin: 0;
  height: 0.9rem;
  padding: 0 0.3rem;
  line-height: 0.9rem;
  font-size: 0.24rem;
  color: #1C1C1C;
}
.weui-cells_radio .vux-radio-label {
  font-size: 0.24rem;
  line-height: 0.9rem;
  height: 0.9rem;
  display: block;
}
.weui-icon-checked {
  display: block!important;
  line-height: 0.9rem;
}
.weui-cells_radio .weui-check:checked + .weui-icon-checked:before {
  color: #523F3F!important;
  line-height: 0.9rem;
}
.weui-grids:before {
  border-top: 1px solid #DBD7D7!important;
}
.weui-grid:after {
  border-bottom: 1px solid #DBD7D7!important;
}
/*下拉菜单*/
.tab-sort {
  border-bottom: 1px solid #DBD7D7;
  overflow: hidden;
  background: #fff;
}
.tab-sort li {
  float: left;
  width: 1.7rem;
  margin: 0.18rem 0;
  position: relative;
  padding: 0 0.4rem;
}
.tab-sort li:nth-child(2) {
  border-left: 1px solid #DBD7D7;
  border-right: 1px solid #DBD7D7;
  width: 1.58rem;
}
.tab-sort .iconfont {
  position: absolute;
  font-size: 0.12rem;
  top: 0;
  right: 0.2rem;
}
.tab-sort .weui-cell {
  padding: 0;
}
.tab-sort .vux-cell-value {
  font-size: 0.28rem;
  display: block;
  color: #777D84;
  width: 1.32rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.tab-sort .weui-cell__ft {
  text-align: center;
  width: 100%;
  padding: 0;
  height: 0.4rem;
}
.tab-sort .weui-cell_access .weui-cell__ft:after {
  font-family: 'iconfont';
  content: "\e603";
  border: 0;
  -webkit-transform: none;
          transform: none;
  width: 0.12rem;
  height: 0.06rem;
  font-size: 0.24rem;
  top: 25%;
}
/*横向滚动标签*/
.tip-list {
  margin: 0.3rem 0.4rem;
  padding: 0!important;
  position: relative;
  height: 0.63rem;
}
.tip-list .vux-tab-container {
  height: 0.63rem;
  padding: 0;
  line-height: 0.63rem;
}
.tip-list .vux-tab {
  height: 0.63rem;
  line-height: 0.63rem;
}
.tip-list .scrollable {
  padding: 0;
}
.tip-list .scrollable .vux-tab-item {
  height: 0.63rem;
  line-height: 0.63rem;
  border-radius: 0.1rem;
  background: #EAEDEB;
  color: #777D84;
  margin-right: 0.24rem;
  -webkit-box-flex: 0;
  -webkit-flex: 0 0 20%;
          flex: 0 0 20%;
  padding: 0 0.1rem;
}
.tip-list .vux-tab .vux-tab-item.vux-tab-selected {
  border: 0;
  color: #fff;
  background: #487D61;
}
.tip-list .scrollable .vux-tab-ink-bar {
  display: none!important;
}
/*正在加载*/
.weui-loadmore {
  width: 100%!important;
  height: 0.6rem;
  line-height: 0.6rem!important;
  background: #EAEDEB;
  border: 0;
  border-left: 0;
  border-right: 0;
  font-size: 0.24rem;
  color: #A7ABAF;
  line-height: 0.6rem;
  margin: 0!important;
}
.weui-loadmore .weui-loadmore__tips {
  font-size: 0.24rem;
  color: #A7ABAF;
}
/*头图*/
.headImg {
  width: 6.7rem;
  height: 2.8rem;
  overflow: hidden;
  margin: 0.4rem auto;
  position: relative;
  padding: 0 0.4rem;
}
.headImg img {
  width: 100%;
  height: 100%;
  border-radius: 0.1rem;
}
.header {
  width: 100%;
  height: 1rem;
  background: #F5F2F2;
  /*顶部搜索框*/
}
.header .vux-search-box {
  height: 1rem;
  width: 100%;
  background: #F6F8F6;
  text-align: center;
  /*选中状态*/
}
.header .vux-search-box .weui-search-bar {
  height: 100%;
  width: 100%;
  background: #F6F8F6;
  padding: 0;
}
.header .vux-search-box .weui-search-bar .vux-search-mask {
  width: 100%;
}
.header .vux-search-box .weui-search-bar .weui-search-bar__input {
  vertical-align: top;
}
.header .vux-search-box .weui-search-bar .weui-search-bar__label .weui-icon-search,
.header .vux-search-box .weui-search-bar .weui-search-bar__label span {
  vertical-align: top;
  line-height: 1rem;
}
.header .vux-search-box .weui-search-bar .weui-search-bar__label {
  background: #F6F8F6;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0;
  font-size: 0.28rem;
  color: #757171;
}
.header .vux-search-box .weui-search-bar .weui-icon-clear {
  top: 0.2rem;
}
.header .vux-search-box .weui-search-bar .weui-icon-search {
  margin-right: 0.2rem;
  color: #487D61;
  font-weight: bold;
}
.header .vux-search-box .weui-search-bar .weui-search-bar__cancel-btn {
  font-size: 0.28rem;
  line-height: 1rem;
  margin-right: 0.2rem;
  color: #9c5640;
}
.header .vux-search-box .weui-search-bar .weui-search-bar__box {
  background: #F6F8F6;
  line-height: 1rem;
  padding: 0 0.8rem;
}
.header .vux-search-box .weui-search-bar .weui-search-bar__box .weui-icon-search {
  line-height: 1rem;
}
.header .vux-search-box .weui-search-bar .weui-search-bar__box .weui-search-bar__input {
  line-height: .4rem;
  padding: 0;
  font-size: 0.28rem;
  height: 0.4rem;
  margin: 0.3rem 0 0;
  width: 6rem;
}
.header .vux-search-box .weui-search-bar__form {
  width: 5.15rem;
  overflow: hidden;
}
.header .vux-search-box .weui-search-bar_focusing {
  padding: 0;
}
.header .vux-search-box .weui-search-bar_focusing .weui-icon-search {
  float: left;
  left: 0;
  margin-right: 0.2rem;
  display: none;
}
.header .vux-search-box .weui-search-bar_focusing .weui-search-bar__input {
  width: auto;
  float: left;
  padding: 0 0.4rem!important;
  font-size: 0.3rem!important;
}
.header .vux-search-box .weui-search-bar_focusing .weui-search-bar__box {
  padding: 0;
}
.header .vux-search-box .weui-search-bar:before,
.header .vux-search-box .weui-search-bar:after {
  border: 0;
}
.header .home-logo {
  width: 2.34rem;
  height: 1rem;
  float: left;
  text-align: center;
  background: #fff;
}
.header .home-logo img {
  width: 100%;
  height: 100%;
  display: block;
}
.text-center {
  text-align: center;
}
/*日历*/
.calender-box {
  overflow: hidden;
  border-bottom: 1px solid #d8d8d8;
}
.calender-box .calender-header {
  border: 1px solid #d8d8d8;
  border-left: 0;
  border-right: 0;
  padding: 0 0.4rem;
  width: auto;
}
.calender-box .calender-header .flex-demo {
  color: #A7ABAF;
  font-size: 0.24rem;
  text-align: center;
  line-height: 0.64rem;
  font-weight: bold;
}
.calender-box .calender-main {
  overflow: hidden;
  padding: 0 0 0.4rem 0.4rem;
  height: 3rem;
  width: auto;
}
.calender-box .calender-main .vux-checker-item {
  float: left;
  width: 0.72rem;
  margin: 0.4rem 0.28rem 0 0;
  background: #fff;
  height: auto;
  line-height: 1;
  padding: 0;
}
.calender-box .calender-main .vux-checker-item .calender-date {
  background: #EAEDEB;
  border-radius: 0.08rem;
  width: 0.72rem;
  height: 0.72rem;
  font-size: 0.28rem;
  color: #777D84;
  line-height: 0.72rem;
  margin-bottom: 0.2rem;
  text-align: center;
}
.calender-box .calender-main .vux-checker-item .calender-title {
  font-size: 0.24rem;
  color: #A7ABAF;
  text-align: center;
  line-height: 1;
}
.calender-box .calender-main .calenderChecker-item-selected .calender-date {
  background: #EFC663;
  color: #fff;
}
.calender-box .calender-main .calenderChecker-item-disabled .calender-date {
  color: #C0C3C7;
}
.calender-box .show-more {
  font-size: 0.24rem;
  color: #777D84;
  text-align: center;
  display: block;
  height: 0.76rem;
  line-height: 0.76rem;
  margin: 0 0.3rem;
  border-top: 1px solid #d8d8d8;
}
/*按钮*/
.submit {
  background: #487D61;
  border-radius: 0.1rem;
  color: #FFF;
  text-align: center;
  width: 6.7rem;
  height: 0.8rem;
  line-height: 0.8rem;
  margin: 0 auto;
  display: block;
  font-size: 0.32rem;
}
/*公共短图列表样式*/
.venueActivity-box {
  padding: 0.4rem;
  border-bottom: 1px solid #DBD7D7;
  position: relative;
  overflow: hidden;
}
.venueActivity-box .venueActivity-img {
  float: left;
  width: 2.2rem;
  height: 1.74rem;
  border-radius: 0.1rem;
  overflow: hidden;
  margin: 0 0.2rem 0 0;
  position: relative;
  /**角标**/
}
.venueActivity-box .venueActivity-img .superscript {
  background: rgba(28, 28, 28, 0.6);
  width: 100%;
  height: 0.4rem;
  text-align: center;
  font-size: 0.22rem;
  line-height: 0.4rem;
  color: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
}
.venueActivity-box .venueActivity-msg {
  float: left;
}
.venueActivity-box .venueActivity-msg h3 {
  color: #1c1c1c;
  font-size: 0.28rem;
  width: 4.3rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 0.4rem;
  font-weight: normal;
}
.venueActivity-box .venueActivity-msg h3.short {
  width: 3.3rem;
}
.venueActivity-box .venueActivity-msg p {
  margin: 0.2rem 0;
  font-size: 0.24rem;
  color: #777D84;
  width: 4.3rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 1;
}
.venueActivity-box .venueActivity-msg p:last-child {
  margin: 0.2rem 0 0;
}
.venueActivity-box .venueActivity-msg .no-price,
.venueActivity-box .venueActivity-msg .have-price {
  font-size: 0.28rem;
  margin-top: 0.5rem;
}
.venueActivity-box .venueActivity-msg .range {
  margin: 0.6rem 0 0!important;
  color: #A4644D;
}
.venueActivity-box .venueActivity-msg .range span {
  color: #777D84;
  margin: 0 0.1rem 0 0;
  width: 1.66rem;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.venueActivity-box .corner-tip {
  position: absolute;
  right: 0.4rem;
  bottom: 0.4rem;
  font-size: 0.28rem;
}
.venueActivity-box .corner-tip-top {
  position: absolute;
  right: 0.4rem;
  top: 0.45rem;
  font-size: 0.28rem;
  line-height: 1;
}
.venueActivity-box .no-price {
  color: #57A496;
}
.venueActivity-box .have-price {
  color: #E05163;
}
.venueActivity-box .corner-btn {
  display: block;
  position: absolute;
  width: 1.4rem;
  height: 0.63rem;
  line-height: 0.63rem;
  text-align: center;
  color: #FFFFFF;
  border-radius: 0.08rem;
  background: #487D61;
  right: 0.4rem;
  bottom: 0.4rem;
  font-size: 0.28rem;
}
.venueActivity-box .corner-btn.disable {
  background: #F2F2F2;
  color: #777D84;
  width: 1.6rem;
}
.venueActivity-box .weui-btn {
  position: absolute;
  right: 0.4rem;
  bottom: 0.4rem;
  width: 1.4rem;
  height: 0.63rem;
  border-radius: 0.08rem;
  font-size: 0.28rem;
  padding: 0;
  border: 0;
}
.venueActivity-box .weui-btn:active {
  background: #B3866D;
  color: #fff;
}
.venueActivity-box .weui-btn:after {
  border: 0;
}
.venueActivity-box .weui-btn_default {
  background: #B3866D;
  color: #fff;
}
.venueActivity-box .weui-btn_disabled {
  background: #F2F2F2;
  color: #757171;
}
/*大图列表样式*/
.card-box:first-child {
  padding-top: 0;
}
.card-box {
  border-bottom: 1px solid #D8D8D8;
  padding: 0 0 0.4rem;
  position: relative;
  margin: 0 0 0.4rem;
}
.card-box .card-banner {
  width: 6.7rem;
  height: 2.8rem;
  margin: 0 auto 0.4rem;
  overflow: hidden;
  position: relative;
  border-radius: 0.1rem;
}
.card-box .card-banner img {
  width: 6.7rem;
  height: 2.8rem;
}
.card-box .card-banner .mask {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 0.6rem;
  background: rgba(28, 28, 28, 0.6);
  color: #fff;
  font-size: 0.24rem;
  text-align: center;
  line-height: 0.6rem;
}
.card-box h3 {
  font-size: 0.34rem;
  color: #1C1C1C;
  line-height: 1;
  width: 4.76rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: 0 0 0.2rem 0.4rem;
  font-weight: normal;
}
.card-box p {
  color: #777D84;
  font-size: 0.24rem;
  margin: 0 0 0.2rem 0.4rem;
  line-height: 1;
}
.card-box p span {
  margin: 0 0 0 0.2rem;
}
.card-box .card-txt {
  color: #777D84;
  font-size: 0.24rem;
  width: 4.76rem;
  line-height: 1;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: 0 0 0 0.4rem;
}
.card-box .card-txt i {
  margin-right: 0.15rem;
  font-size: 0.24rem;
  color: #A7ABAF;
}
.card-box .corner {
  position: absolute;
  right: 0.4rem;
  bottom: 0.4rem;
  height: 0.9rem;
}
.card-box .corner h4 {
  font-size: 0.24rem;
  color: #1C1C1C;
  line-height: 1;
  text-align: right;
  font-weight: normal;
}
.card-box .corner h4 span {
