
.centerPhone-item {
  height: 0.78rem;
  line-height: 0.78rem;
  border-bottom: 1px solid #d8d8d8;
  padding: 0;
}
.centerPhone-item label {
  font-size: 0.24rem;
  color: #777D84;
  height: 0.52rem;
  line-height: 0.52rem;
  border-right: 1px solid #d8d8d8;
  margin: 0.13rem 0.2rem 0 0;
  float: left;
  width: 1.64rem;
}
.centerPhone-item .centerPhone-input {
  float: left;
  padding: 0;
  height: 0.52rem;
  line-height: 0.52rem;
  margin: 0.13rem 0 0 0;
  font-size: 0.24rem;
  width: 4rem;
  overflow: hidden;
}
.centerPhone-item .mini-input {
  width: 2.3rem;
}
.centerPhone-item .weui-cell:before {
  border: 0!important;
}
.centerPhone-item .get-valid {
  float: right;
  width: 1.86rem;
  height: 0.48rem;
  line-height: 0.48rem;
  border-radius: 0.1rem;
  color: #FFFFFF;
  background: #A1C07A;
  font-size: 0.24rem;
  text-align: center;
  margin: 0.13rem 0 0;
}
.centerPhone-item .showBox {
  width: 100%;
  bottom: 0;
  top: 3.5rem;
  background: #fff;
  overflow: hidden;
}
.centerPhone-item .showBox .showBox-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1.5rem;
}
.centerPhone-item .showBox .showBox-header h3 {
  text-align: center;
  color: #777D84;
  font-size: 0.28rem;
  display: block;
  line-height: 0.78rem;
  font-weight: normal;
}
.centerPhone-item .showBox .showBox-header .icon-clos {
  position: absolute;
  right: 0.4rem;
  top: 0.2rem;
  font-size: 0.34rem;
  color: #666;
  line-height: 0.34rem;
}
.centerPhone-item .showBox .showBox-header-list {
  padding: 0 0.4rem;
  overflow: hidden;
  border-bottom: 1px solid #d8d8d8;
}
.centerPhone-item .showBox .showBox-header-list li {
  float: left;
  max-width: 25%;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  line-height: 0.72rem;
  position: relative;
}
.centerPhone-item .showBox .showBox-header-list li a {
  display: block;
  color: #1C1C1C;
  font-size: 0.28rem;
  width: 100%;
}
.centerPhone-item .showBox .showBox-header-list li:last-child a {
  color: #487D61;
}
.centerPhone-item .showBox .showBox-header-list .active {
  border-bottom: 0.04rem solid #487D61;
}
.centerPhone-item .showBox .showBox-list {
  position: absolute;
  top: 1.5rem;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: auto;
  padding: 0 0.3rem;
}
.centerPhone-item .showBox .showBox-list li {
  height: 0.78rem;
  line-height: 0.78rem;
  text-align: left;
  color: #777D84;
  font-size: 0.24rem;
  float: none;
  border-bottom: 1px solid #d8d8d8;
  position: relative;
}
.centerPhone-item .showBox .showBox-list li i {
  position: absolute;
  right: 0.1rem;
  top: 0;
}



